import { toast } from "react-toastify";
import {FaCheckCircle} from "react-icons/fa"


export const notificacion = (status, msg) => {

  if (status === 200) {
    toast.success(msg, {
      position: toast.POSITION.TOP_RIGHT,
      autoClose: 4000,
    });
  } else {
    toast.error(msg, {
      position: toast.POSITION.TOP_RIGHT,
    });
  }
};
